document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
});

function iniciarApp() {
    navegacionFija();
    crearGaleria();
    scrollNav(); //smooth scroll
}

function navegacionFija() {
    const barra = document.querySelector('.header');

    //Elemento hasta el que se debe de dar scroll
    const sobreFestival = document.querySelector('.sobre-festival');

    const body = document.querySelector('body');

    //Escuchamos el scroll
    window.addEventListener('scroll', function(){
        //Ponemos el elemento que vamos a estar monitoreando (nos da un DOM rect)
        //console.log( sobreFestival.getBoundingClientRect() );

        //Si toca la parte superior del elemento agregamos una clase 
        if( sobreFestival.getBoundingClientRect().top < 0 ) {
            barra.classList.add('fijo');
            body.classList.add('body-scroll');
        }
        //Si ya no estamos en esa sección borramos la clase 
        else {
            barra.classList.remove('fijo');
            body.classList.remove('body-scroll');
        }
    })
}

function scrollNav() {
    const enlaces = document.querySelectorAll('.navegacion-principal a'); //Seleccionamos enlaces de la clase .navegacion principal
    enlaces.forEach(enlace => { //Iterar cada enlace (no se puede el eventlistener sin iterar)
        enlace.addEventListener('click', function(e) { //Agregamos un event listener de click
            e.preventDefault(); //Para habilitar el scroll smooth
            //console.log(e.target.attributes.href.value);Para leer y mostrar en consola el atributo href
            const seccionScroll = e.target.attributes.href.value; //Obtenemos el valor de href
            const seccion = document.querySelector(seccionScroll); //Seleccionamos en el documento
            seccion.scrollIntoView( { behavior: "smooth" }); //Comportamiento del scroll
        })

    });
}

function crearGaleria() {
    const galeria = document.querySelector('.galeria-imagenes')

    for (let i = 1; i <= 12; i++) {
        const imagen = document.createElement('PICTURE');
        imagen.innerHTML =`
            <source srcset="build/img/thumb/${i}.avif" type = "image/avif">
            <source srcset="build/img/thumb/${i}.webp" type = "image/webp">
            <img loading ="lazy" width="200" height="300" src="build/img/thumb/${i}.jpg" alt=" imagen galeria">
        `;

        imagen.onclick = function() { //Para pasarle parámetros tenemos que llamarlo como callback
            mostrarImagen(i);
        }
        galeria.appendChild(imagen);

    }
}

function mostrarImagen( id ) { //el id sirve para identificar el número de la imagen
    const imagen = document.createElement('PICTURE');
    imagen.innerHTML =`
    <source srcset="build/img/grande/${id}.avif" type = "image/avif">
    <source srcset="build/img/grande/${id}.webp" type = "image/webp">
    <img loading ="lazy" width="200" height="300" src="build/img/grande/${id}.jpg" alt=" imagen galeria">
    `;

    //Crea overlay con la imagen
    const overlay = document.createElement('DIV');
    overlay.appendChild(imagen); //Agregamos la imagen al overlay
    overlay.classList.add('overlay'); //Le ponemos la clase de overlay
    overlay.onclick = function() { //Para quitar la imagen al hacer click en cualquier parte
        const body = document.querySelector('body');
        body.classList.remove('fijar-body'); //Quita la clase al hacer click
        overlay.remove(); //Con esto quitamos la imagen en grande
    }

    //Boton para cerrar el modal (ventana modal)
    const cerrarModal = document.createElement('P');
    cerrarModal.textContent = 'X'; //Para que sea la tache para cerrar
    cerrarModal.classList.add('btn-cerrar'); //Agregamos una clase para que se pueda manipular con scss
    cerrarModal.onclick = function() {
        const body = document.querySelector('body');
        body.classList.remove('fijar-body'); //Quita la clase al hacer click
        overlay.remove(); //Con esto quitamos la imagen en grande
    }
    overlay.appendChild(cerrarModal); //Para agregarlo al overlay

    //Añadirlo al HTML
    const body = document.querySelector('body'); //Para seleccionar la etiqueta de body
    body.appendChild(overlay); //Le agregamos al body el overlay (se agrega al final)
    body.classList.add('fijar-body'); //Es para después modificarlo y no permitir scroll
}